@import url(../../../../variables.css);

.experiment_description {
    background-color: var(--background_color);
    padding: 2%;
    width: 65%;
    color: var(--font_color);
    font-family: Roboto;
    font-size: var(--font_xs);
    font-weight: 400;
    text-align: left;
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    overflow-y: scroll;
    padding-bottom: 0;

    white-space: nowrap;
    overflow-x: scroll;
}
.experiment_description h1 {
    font-size: var(--font_s);
    margin: 0;
    margin-bottom: 0.5em;
    margin-top: 0.5em;
    text-align: center;
}

/* Im overwriting the p tag so it doesn't overflow */
.experiment_description p {
    margin: 0;
    display: inline;
    word-wrap: break-word;
    white-space: break-spaces;
}
