/* This file tracks all css variables in the project */

html {
    --background_color: #ffffff; /* Background color of the components */
    --primary_color: #444444; /* Primary color, for example this is the color of the headers */
    --primary_color_shader: #525252; /* Search bar color */
    --secondary_color: #888888; /* Search button color */
    --button_active_color: #ffffff;
    --button_inactive_color: #cecece;
    --background2_color: #dfdfdf; /* Second background color of the components */
    --content_background_color: #f2f2f2; /* Background color of the content area */
    --font_color: #444444; /* Main font color */
    --font_color_inverted: #f2f2f2; /* Inverted font color */
    --invert_icon: 0%; /* Auto invert color of icons */
    --positive_button_color: #0075ff; /* Color of positive buttons, such as go, start etc. */
    --active_color: #1797f85c; /* Color when you press a button */
    --logo_schumacher: url(./components/sideBar/resources/LogoSchumacher.png);

    --animation_fast: 150ms;
    --animation_slow: 350ms;

    /*
    --font_xxs: 1em;
    --font_xs: 1.2em;
    --font_s: 1.6em;
    --font_m: 2em;
    --font_l: 3.2em;
    --font_xl: 4em;
    */

    --font_xxs: 12px;
    --font_xs: 14px;
    --font_s: 18px;
    --font_m: 20px;
    --font_l: 24px;
    --font_xl: 45px;

    --num_columns: 13;
    --num_rows: 13;

    --num_columns_sub_page: 12;
    --num_rows_sub_page: 14;

    --zoom_scale: 1.4;
}

